#dx-emp-pers-docs-panel{
    padding-left: 15px;
    padding-right: 15px;
}

#dx-emp-pers-docs-table-history, #dx-emp-pers-docs-new-row{
    display: none;
}

.dx-emp-pers-docs-country-row{
    margin-bottom: 10px;
}

.dx-emp-pers-docs-label-mobile{
    margin-top: 5px;
}

.dx-emp-pers-docs-file-input-remove-btn i{
    color: #333;
}

.dx-emp-pers-docs-type-label{
    padding-top: 10px; 
    font-weight: bold;
    text-transform: uppercase;
}

#dx-emp-pers-docs-table{
    margin-top: 10px;
}

.dx-emp-pers-docs-table-col{
    padding-left: 5px;
    padding-right: 5px;
}

.dx-emp-pers-docs-table-col1{
    padding: 0;
}

.dx-emp-pers-docs-table-col2{
    padding-left: 10px;
    padding-right: 0;
}

@media screen and (max-width: 1199px) {
    .dx-emp-pers-docs-table-col2{
        padding-left: 0;
        padding-right: 0;
    }
}

@media screen and (max-width: 1600px) {
    .dx-emp-pers-docs-file-input-download{
        max-width: 220px;
    }
}
@media screen and (max-width: 1500px) {
    .dx-emp-pers-docs-file-input-download{
        max-width: 180px;
    }
}
@media screen and (max-width: 1300px) {
    .dx-emp-pers-docs-file-input-download{
        max-width: 150px;
    }
}
@media screen and (max-width: 1199px) {
    .dx-emp-pers-docs-file-input-download{
        max-width: 300px;
    }
}
@media screen and (min-width: 1600px) {
    .dx-emp-pers-docs-file-input-download{
        max-width: 280px;
    }
}

